@import './style-vars/fonts.scss';

html{
  box-sizing: border-box;
}
*, *:before, *::after{
  box-sizing: inherit;
}


html,
body,
p,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
button{
  padding: 0px;
  margin: 0px;
  outline: none;
  border: 0px;
}

a{
  text-decoration: none;
  outline: none;
  border: 0px;
}

ul, 
ol,
li{
  list-style-type: none ;
}

.app-content{
  margin-left: 88px;
  background: $main-bg;
  min-height: 100vh;
}

body{
  @extend %default-font;
}

@media(max-width: 600px){
  .app-content{
    margin-left: 68px;
  }
}
