.search {
    display: flex;
    align-items: center;

    &__input {
        width: 350px;
        padding: 10px;
        outline: 0;
        border: 0;

        &-btn {
            background: transparent;
            margin: 0px 10px;
            cursor: pointer;
        }
    }

    @media (max-width: 800px) {
        &__input {
            width: 250px;
        }
    }
}